import React, { Component } from 'react';
import GoogleMapReact from 'google-map-react';
import './GoogleMap.scss';

const AnyReactComponent = ({ text }) => {
  return (
    <div
      style={{
        display: 'block',
        wordBreak: 'keep-all',
        fontSize: '1.2rem',
        width: '12em',
        textShadow: `-1px 0 2px white,
          1px 0 3px white,
          -1px 0 3px white,
          0 -1px 3px white,
          0 1px 3px white`
      }}>
      {text}
    </div>
  );
};
 
class GoogleMap extends Component {
  static defaultProps = {
    center: {
      lat: 37.5652894,
      lng: 126.8494666
    },
    zoom: 11
  };

  render() {
    const { location } = this.props;
    const center = {
      lat: location.lat,
      lng: location.lng
    }
    const zoom = this.props.location.zoom;
    const placeName = this.props.placeName;

    return (
      // Important! Always set the container height explicitly
      <div className="google-map">
        <GoogleMapReact
          bootstrapURLKeys={{ key: 'AIzaSyCP0mceYvdST7OC5bp_Pf_dHcgEFETiPm0' }}
          defaultCenter={this.props.center}
          center={center}
          defaultZoom={this.props.zoom}
          zoom={zoom}
        >
          <AnyReactComponent
            lat={location.lat}
            lng={location.lng}
            text={`★${placeName}`}
          />
        </GoogleMapReact>
      </div>
    );
  }
}

export default GoogleMap;



